body {
  margin: 0;
  padding: 0;
  font-family:"Courier New", Courier, monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(right, rgb(112, 228, 228), rgb(236, 98, 125));
  background: linear-gradient(to left, rgb(112, 228, 228), rgb(236, 98, 125));
}

button {
  cursor: pointer;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}
.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top:0;
  left:0;
  bottom:0;
  right:0;
  z-index: -1;
}

.Tilt {
    display:inline-block;
}
.Tilt-inner {
    background: -webkit-linear-gradient(left, rgb(38, 44, 136),rgb(78, 78, 78));
    background: linear-gradient(to right, rgb(38, 44, 136),rgb(78, 78, 78));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: bold;
}
.form {
    width: 700px;
    background:
-webkit-linear-gradient(63deg, #151515 5px, transparent 5px) 0 5px,
-webkit-linear-gradient(243deg, #151515 5px, transparent 5px) 10px 0px,
-webkit-linear-gradient(63deg, #222 5px, transparent 5px) 0px 10px,
-webkit-linear-gradient(243deg, #222 5px, transparent 5px) 10px 5px,
-webkit-linear-gradient(left, #1b1b1b 10px, transparent 10px),
-webkit-linear-gradient(#1d1d1d 25%, #1a1a1a 25%, #1a1a1a 50%, transparent 50%, transparent 75%, #242424 75%, #242424);
    background:
linear-gradient(27deg, #151515 5px, transparent 5px) 0 5px,
linear-gradient(207deg, #151515 5px, transparent 5px) 10px 0px,
linear-gradient(27deg, #222 5px, transparent 5px) 0px 10px,
linear-gradient(207deg, #222 5px, transparent 5px) 10px 5px,
linear-gradient(90deg, #1b1b1b 10px, transparent 10px),
linear-gradient(#1d1d1d 25%, #1a1a1a 25%, #1a1a1a 50%, transparent 50%, transparent 75%, #242424 75%, #242424);
background-color: #131313;
background-size: 20px 20px;
}
.imageContainer {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}
@media (max-width:1024px) {
    .imageContainer {
        display: block;
        position: absolute;
    }
}
.currentImage {
    width:auto;
    height: 500px;
}

@media (max-width: 600px) {
    .currentImage {
        width:90%;
        height:auto;
    }
}
.info {

    background: rgba(255, 255, 255, 0.144);
    height:100%;
    -webkit-transition: all 1s;
    transition: all 1s;
    padding:10px;
}
@media (max-width:1024px) {
.info {
    position: absolute;
    right:0;
    top:0; 
}
}
@media (max-width:600px) {
    .info {
        margin:0 auto;
        position: static;
        width:90%;
        height:auto;
    }
}
.info:hover {
    background: rgba(255, 255, 255, 0.603);
}
.info-paragraph {
    display: flex;
    justify-content: space-between;
    padding:2px;
    margin:0;
}

.detectedName {
    color:rgb(3, 4, 109);
    
}
.detectedValue {
    padding-left:2px;
}
